import React, { useState } from "react";
import { makeStyles } from "@mui/styles";
import useTable from "../controls/useTable";
import * as TotalServices from "../services/TotalServices";
import {
  Button,
  InputAdornment,
  Paper,
  TableBody,
  TableCell,
  TableRow,
  TextField,
  Toolbar,
} from "@mui/material";
import { DeleteOutline, Search } from "@mui/icons-material";

const useStyles = makeStyles((theme) => ({
  pageContent: {
    margin: "1rem",
    padding: "1rem",
  },
  item: {
    margin: ".5rem",
    textAlign: "center",
  },
  tableCEll: {
    padding: "0px 12px 0px 0px",
  },
}));

const headCells = [
  { id: "foods", label: "سفارش" },
  { id: "totalPrice", label: "قیمت" },
  { id: "Date", label: "تاریخ" },
  { id: "Actions", label: "تنظیمات" },
];

export default function DailyPrice(props) {
  const classes = useStyles();  
  const [records, setRecords] = useState(TotalServices.getAllTotals());
  const [filterFn, setFilterFn] = useState({
    fn: (item) => {
      return item;
    },
  });

  const { TblContainer, TblHead, TblPagination, recordsAfterPagingAndSorting } =
    useTable(records, headCells, filterFn);

  const handleSearch = (e) => {
    setFilterFn({
      fn: (items) => {
        if (e.target.value === "") return items;
        else return items.filter((x) => x.totalPrice.includes(e.target.value));
      },
    });
  };

  const onDelete = (id) => {
    TotalServices.deleteTotal(id);
    setRecords(TotalServices.getAllTotals());
  };

  return (
    <>
      <Paper sx={{ margin: "5rem" }}>
        <Toolbar>
          <TextField
            style={{ direction: "rtl" }}
            label="جستجو"
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Search />
                </InputAdornment>
              ),
            }}
            onChange={handleSearch}
          />
        </Toolbar>
        <TblContainer>
          <TblHead />
          <TableBody>
            {recordsAfterPagingAndSorting().map((item) => (
              <TableRow key={item.id}>
                <TableCell className={classes.tableCell}>
                  {item.foods?.map((f) => f.FName + " - ")}
                </TableCell>
                <TableCell className={classes.tableCell}>
                  {item.totalPrice}
                </TableCell>
                <TableCell className={classes.tableCell}>{item.Date}</TableCell>
                <TableCell className={classes.tableCell}>
                  <Button color="error" onClick={()=>onDelete(item.id)}><DeleteOutline/></Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </TblContainer>
        <TblPagination />
      </Paper>
    </>
  );
}
