import React from "react";
import ImageListItem from "@mui/material/ImageListItem";
import ImageListItemBar from "@mui/material/ImageListItemBar";
import IconButton from "@mui/material/IconButton";
import { Grid, Tooltip } from "@mui/material";
import { ShoppingCart } from "@mui/icons-material";

export default function FoodItem(props) {
  const { food, onAdd } = props;
  return (
    <Grid
      container
      sx={{ width: "auto", height: 150, marginTop: "2rem", margin: "1rem" }}
    >
      <ImageListItem>
        {food.FImage ? (
          <Tooltip title={food.FName}>
            <Grid item>
              <img
                src={food.FImage}
                alt={food.FName}
                loading="lazy"
                style={{
                  objectFit: "containt",
                  height: "150px",
                  maxHeight: "150px",
                  width: "150px",
                  maxWidth: "200px",
                }}
              />
            </Grid>
          </Tooltip>
        ) : (
          <Tooltip title={food.FName}>
            <Grid item>
              <img
                src="https://upload.wikimedia.org/wikipedia/commons/thumb/6/65/No-Image-Placeholder.svg/1665px-No-Image-Placeholder.svg.png"
                alt="notImage"
                loading="lazy"
                style={{
                  objectFit: "containt",
                  height: "150px",
                  maxHeight: "150px",
                  width: "150px",
                  maxWidth: "200px",
                }}
              />
            </Grid>
          </Tooltip>
        )}
          <ImageListItemBar
            title={food.FName}
            subtitle={food.FPrice}
            actionIcon={
              <IconButton
                sx={{ color: "rgba(255, 255, 255, 0.54)" }}
                aria-label={`info about ${food.FPrice}`}
                onClick={() => onAdd(food)}
              >
                <ShoppingCart />
              </IconButton>
            }
          />
      </ImageListItem>
    </Grid>
  );
}
