import React, { useState } from "react";
import {
  Button,
  ButtonGroup,
  Grid,
  TextField,
  Typography,
} from "@mui/material";
import Foods from "../menu/Foods";

export default function Bascket(props) {
  const { cartItem, onAdd, onRemove } = props;
  const [turn, setTurn] = useState(1);
  const itemsPrice = cartItem.reduce((a, c) => a + c.FPrice * c.qty, 0);
  // const taxPrice = itemsPrice * 0.09;
  // const shippingPrice = itemsPrice > 2000 ? 0 : 50;
  const totalPrice = itemsPrice;
  const addTurn=()=>{
    setTurn(turn+1)
  }
  return (
    <Grid container style={{ textAlign: "center", alignContent: "center" }}>
      <Typography variant="h2">Order</Typography>
      {cartItem?.length === 0 && (
        <Grid
          container
          spacing={2}
          style={{ marginLeft: "0.5rem", margin: "1rem" }}
        >
          <div>لیست سفارش خالی است</div>
        </Grid>
      )}
      {cartItem &&
        cartItem.map((item) => (
          <Grid
            container
            spacing={2}
            style={{ marginLeft: "0.5rem", margin: "1rem" }}
            key={item.id}
          >
            <Grid item minWidth={106}>
              <Typography variant="p">{item.FName}</Typography>
            </Grid>
            <Grid item>
              <ButtonGroup size="small">
                <Button onClick={() => onRemove(item)}>-</Button>
                <Button disabled>{item.qty}</Button>
                <Button onClick={() => onAdd(item)}>+</Button>
              </ButtonGroup>
            </Grid>
            <Grid item>
              {item.qty} x {Number(item.FPrice).toLocaleString()} تومان
            </Grid>
          </Grid>
        ))}
      {cartItem.length !==0 && (
        <TextField
          id="form-texts-toman"
          variant="outlined"
          value={turn}
          onChange={(e) => setTurn(e.target.value)}
          name="turn"
          label="نوبت"
          type="number"
          inputProps={{min:0}}
          style={{ borderRadius: "50", backgroundColor: "white" }}
        />
      )}
      {cartItem.length !== 0 && (
        <div>
          <Foods
            cartItems={cartItem}
            totalPrice={totalPrice}
            turn={turn}
            addTurn={addTurn}
            itemsPrice={itemsPrice}
          />
        </div>
      )}
    </Grid>
  );
}
